<template>
    <v-form>
        <v-card flat class="ma-5 grey lighten-5">
            <v-card-title>
                <v-spacer></v-spacer>
                <span class="subheading cairo">{{ orderType }}</span>
                <v-spacer></v-spacer>
            </v-card-title>
            <v-card-text>
                <v-layout row wrap>
                    <v-flex
                    xs12
                    md4
                    >
                        <v-text-field
                            v-model="customerName"
                            :rules="customerNameRules"
                            :counter="20"
                            required
                            label="اسم المستخدم *"
                            class="ma-4"
                            @change="change"
                        ></v-text-field>
                    </v-flex>
                    <v-flex
                    xs12
                    md4
                    >
                        <v-text-field
                            v-model="customerZip"
                            :rules="customerZipRules"
                            :counter="15"
                            required
                            label="رقم الهويه *"
                            class="ma-4 black--text"
                        ></v-text-field>
                    </v-flex>
                    <v-flex
                    xs12
                    md4
                    >
                        <v-text-field
                            v-model="customerNationality"
                            :rules="customerNationalityRules"
                            :counter="15"
                            required
                            label="الجنسيه *"
                            class="ma-4 black--text"
                        ></v-text-field>
                    </v-flex>
                    <v-flex
                    xs12
                    md4
                    >
                        <v-text-field
                            v-model="customerAddress"
                            :rules="customerAddressRules"
                            :counter="15"
                            required
                            label="العنوان *"
                            class="ma-4 black--text"
                        ></v-text-field>
                    </v-flex>
                    <v-flex
                    xs12
                    md4
                    >
                        <template>
                            <v-menu
                                ref="menu"
                                v-model="menu"
                                :close-on-content-click="false"
                                :nudge-right="40"
                                lazy
                                transition="scale-transition"
                                offset-y
                                full-width
                                min-width="290px"
                            >
                                <template v-slot:activator="{ on }">
                                    <v-text-field
                                        v-model="date"
                                        label="تارخ الميلاد"
                                        prepend-icon="event"
                                        readonly
                                        v-on="on"
                                        class="ma-4"
                                    ></v-text-field>
                                </template>
                                <v-date-picker
                                ref="picker"
                                v-model="date"
                                :max="new Date().toISOString().substr(0, 10)"
                                min="1950-01-01"
                                @change="save"
                                ></v-date-picker>
                            </v-menu>
                            </template>
                    </v-flex>
                   <v-flex
                    xs12
                    md4
                    >
                        <v-select
                        v-model="select"
                        :items="customerGender"
                        label="الجنس *"
                        required
                        class="ma-4"
                        ></v-select>
                    </v-flex>
                    <v-flex
                    xs12
                    md6
                    >
                    <v-text-field
                        v-model="customerEmail"
                        :counter="15"
                        label="البريد الالكتروني (اختياري)"
                        class="ma-4"
                    ></v-text-field>
                    </v-flex>
                    <v-flex
                    xs12
                    md6
                    >
                    <v-text-field
                        v-model="password"
                        :counter="15"
                        label="كلمه المرور"
                        class="ma-4"
                    ></v-text-field>
                    </v-flex>
                </v-layout>
            </v-card-text>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn flat color="success" @click="submit"><span class="cairo">حفظ البيانات</span></v-btn>
                <v-btn flat color="red" @click="reset"><span class="cairo">مسح الحقول</span></v-btn>
                <v-spacer></v-spacer>
            </v-card-actions>
        </v-card>
    </v-form>
</template>

<script>
import UserServec from '../../services/UserServec'
export default {
    name: 'CardOrderForm',
    props: ['orderType'],
    data(){
        return {
            valid: false,
            customerId: 10,
            customerName: '',
            password: '',
            customerNameRules: [
                v => !!v || '  الاسم مطلوب',
                v => v.length <= 200 || 'يجب ان يكون الاسم اقل من 200 حرف'
            ],
            customerPhone: '0991612921',
            customerPhoneRules: [
                v => !!v || '  رقم الجوال مطلوب',
                v => v.length <= 15 || 'يجب ان يكون الاسم اقل من 15 حرف'
            ],
            customerZip: '',
            customerZipRules: [
                v => !!v || 'رقم الهويه مطلوب',
                v => v.length <= 20 || 'يجب ان يكون رقم الهويه اقل من 20 حرف'
            ],
            customerNationality: '',
            customerNationalityRules: [
                v => !!v || 'الجنسيه مطلوبه',
                v => v.length <= 20 || 'يجب ان تكون الجنسيه اقل من 20 حرف'
            ],
            customerBdateRules: [
                v => !!v || 'تارخ الميلاد مطلوب',
                v => v.length <= 20 || 'يجب ان يكون تاريخ اقل من 20 حرف'
            ],
            customerAddress: '',
            customerAddressRules: [
                v => !!v || 'العنوان مطلوب',
                v => v.length <= 200 || 'يجب ان يكون العنوان اقل من 200 حرف'
            ],
            select: null,
            customerGender: [
                'ذكر',
                'انثى',
            ],
            customerGenderRules: [
                v => !!v || 'الجنس مطلوب',
                v => v.length <= 10 || 'يجب ان يكون الجنس اقل من 10 حرف'
            ],
            customerEmail: '',
            customerEmailRules: [
                v => !!v || 'البريد الالكتروني اختياري',
                v => v.length <= 200 || 'يجب ان يكون البريد الالكتروني اقل من 200 حرف'
            ],
            date: null,
            menu: false,
        }
    },
    methods: {
        save (date) {
            this.$refs.menu.save(date)
        },
        submit () {
            const user = {
                name : this.customerName,
                phone: '456789033222',
                address_text: this.customerAddress,
                birth_date: this.customerBdate,
                gender : this.customerGenderType,
                email: this.customerEmail,
                identity_number: this.customerZip,
                password : this.password,
                city_id: 3
            }
            UserServec.create(user)
            .then(res => { 
                console.log(res.data)
                this.reset();
            })
            .catch(error => console.log(error))
        },
        change () {
            // console.log(this.customerName)
        },
        reset () {
            this.customerName = '';
            this.customerZip = '';
            this.customerNationality = '';
            this.customerAddress = '';
            this.date = null;
            this.select = null;
            this.customerEmail = '';
            this.password = '';
            UserServec.index()
            .then(res => console.log(res.data))
            .catch(error => console.log(error))
        }
    },
    watch: {
        menu (val) {
            val && setTimeout(() => (this.$refs.picker.activePicker = 'YEAR'))
        }
    },
    computed: {
        customerGenderType(){
            return this.select
        },
        customerBdate(){
            return this.date
        }
    }
}
</script>